@import url('https://fonts.googleapis.com/css?family=Nunito&display=swap');
@import url('https://fonts.googleapis.com/css?family=Lexend+Deca&display=swap');

body {
	font-family: 'Nunito', sans-serif;
}

h1 {
	font-family: 'Lexend Deca', sans-serif;
	font-size: 6em;
	margin: 0;
	padding: 0;

}

h2 {
	font-family: 'Lexend Deca', sans-serif;
	font-size: 1.75em;
	font-weight: 400;
	margin: 0;
	padding: 0;
}

h3 {
	font-family: 'Lexend Deca', sans-serif;
	font-size: 1.25em;
	font-weight: 400;
	margin: 0;
	padding: 0;
}

h4 {
	font-family: 'Nunito', sans-serif;
	font-size: 1.25em;
	font-weight: 400;
	margin: 0;
	padding: 0;
}

h5 {
	font-family: 'Nunito', sans-serif;
	font-size: 1em;
	font-weight: 400;
	margin: 0;
	padding: 0;
}

@media screen and (max-width: 1050px) {
	h1 {
	font-size: 3em;		
	}	

	h2 {
		font-size: 1.25em;
	}

	h3 {
		font-size: 1.15em;
	}

	h4 {
		font-size: 1em;
	}

	h5 {
		font-size:.85em;
	}
}

p {
	font-size: 1em;
	line-height:1.5em;
}

