header {
	position:fixed;
	display:block;
	height: 75px;
	width: 100%;
	font-family: 'Lexend Deca', sans-serif;
	background: #000000;
	color: #FFFFFF;
	z-index:9;
	overflow:hidden;


	.logo {
		display: inline-block;
		float: left;
		margin:19px 0 26px 30px;

		.name {
			font-size: 24px;
			color:#FFFFFF;

		}

		.role {
			font-size: 18px;
			color: #8D8D8D;
		}
	}

	nav {
		width: auto;
		height: 75px;
		overflow: hidden;
		padding-left: 26px;
		a {
			display: inline-block;
			color: #FFFFFF;
			font-size: 24px;
			margin: 19px 75px 26px 0;
			float: right;
		}
	}

}

@media screen and (max-width: 550px) {
	header nav a{
	display: none;
	}
}