.vertical-center {
	margin: 0;
	position: absolute;
	top: 50%;
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
}

.center {
	margin: 0;
	position: absolute;
	top: 50%;
	left: 50%;
	-ms-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}

.button {
	background-color: #FFFFFF;
	border-radius: 25px;
	border: none;
	color: #000000;
	height: 50px;
	width: 160px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 18px;
	font-family: 'Nunito', sans-serif;
	font-weight:700;
	margin: 25px;

	&:hover, &:focus{
		background-color: #FFD439;	
	}

	&:active{
		background-color: #FFB405;
	}

	a{
		color: #000000;
	}
}

@media screen and (max-width: 968px) {
	.button {
		height: 40px;
		width: 120px;
		margin: 10px;
	}
}

ul {
	list-style-type: none;
	padding-left: 1.5em;
}