/*horizontal scroll stuff from here on out */

.menu-wrapper {
	position: relative;
	background-color: #1B1B1B;
	padding-top: 50px + 75px;
	padding-bottom: 40px;
	height: auto;
	width:100%;
	overflow-x: hidden;
	overflow-y: hidden;

}

.menu	{
	height: auto; // hide the scroll bar
	box-sizing: border-box;

	white-space: nowrap;
	overflow-x: auto;
	overflow-y: hidden;
	-webkit-overflow-scrolling: touch;

    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none;  /* IE 10+ */

    &::-webkit-scrollbar { /* WebKit */
    width: 0;
    height: 0;
}
	.item {
		display: inline-block;
		color: #FFFFFF;
		text-align: center;
		height: 70vh;
		width: 300px;
		margin: 0 0 0 5vh;
		white-space: normal;

		.screenshot {
			width: auto;
			height: 80%;
		    border-radius: 20px;

		}

	}
}

@media screen and (max-width: 470px) {
	.menu {
		padding: 0px;

		.item {
		margin-left: calc((100% - 300px)/2);
		margin-right: calc((100% - 300px)/2);
	}

		} 
}

.paddles {
	background-color: #000000;
	font-size: 36pt;
}
.paddle {
	position: absolute;
	top: 50%;
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
	width: 45px;
	height:100px;
	background-color: #FFD439;
	box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .25), 0 10px 10px 0 rgba(0, 0, 0, .25);
	border-radius: 0px;
	margin: 0px;
}
.left-paddle {
	left: 0;
	background-image: url("/assets/img/paddle-left.png");
	background-position: center;
	background-size: 21px 70px;
	background-repeat: no-repeat;
}
.right-paddle {
	right: 0;
	background-image: url("/assets/img/paddle-right.png");
	background-position: center;
	background-size: 21px 70px;
	background-repeat: no-repeat;
}
.hidden {
	display: none;
}

.wireframe{
 	max-width: 100%;
 	height: auto;
	box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .5), 0 10px 10px 0 rgba(0, 0, 0, .5);
}

/* Modal stuff from here on out */

/* The Modal (background) */
.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 10; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.9); /* Black w/ opacity */
}

/* Modal Content (image) */
.modal-content {
	margin: auto;
	width: auto;
	max-width: 300px;
	position: absolute;
	top: 0;
	bottom: 0;
	left:0;
	right: 0;
}

@-webkit-keyframes zoom {
  from {-webkit-transform:scale(0)} 
  to {-webkit-transform:scale(1)}
}

@keyframes zoom {
  from {transform:scale(0)} 
  to {transform:scale(1)}
}

/* The Close Button */
.close {
  position: absolute;
  top: 15px;
  right: 35px;
  color: #FFDA19;
  font-size: 40px;
  font-weight: bold;
  transition: 0.3s;
  z-index: 11;
}

.close:hover,
.close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}

/* 100% Image Width on Smaller Screens */
@media only screen and (max-width: 470px){
  .modal-content {
	max-width:100%;
	margin:0 auto;
	}
}
