		.photo{
			max-width: 200px;
		}
		
	}

	#nameNDescription{
		display: inline-block;
		margin: 0 1.5em 1.5em 1.5em;
		vertical-align:middle;

/*	    text-align: left;
*/
	}
	#name {
		max-width: 600px;
	}

	#description {
		max-width: 600px;
		text-align:left;
	}
}

#portfolio{
	background: #454545;
	color: #FFFFFF;
	text-align: center;
}

.project {
	width: 300px;
	text-align: center;
	display: inline-block;
	color: #FFFFFF;
	padding: 30px 3vw 40px 3vw;

	.projectDesc {
		width: 300px;
		height: auto;
		color: #FFFFFF;
	}

	a:focus, a:hover {
		.projectDesc {
			color: #FFD439;	
		}
	}
}


.screenshot{
	height: 50vh;	
    margin-left: auto;
    margin-right: auto;
    -webkit-border-radius: 2.5vh;
    -moz-border-radius: 2.5vh;
    border-radius: 2.5vh;

	box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .8), 0 10px 10px 0 rgba(0, 0, 0, .8);
}

#contactAndResume {
	color: #FFFFFF;
	text-align: center;
}

#resume {
	position: relative;
	top: 0px;
	background-color: #1B1B1B;
	height:50%;
	margin: 0;
	padding: 0;
	color: #FFFFFF;

}

#innerResume {
	margin: 0;
	color: #FFFFFF;
	position: relative;
	top: 50%;
	left: 50%;
	-ms-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}

#contact {
	background-color: #000000;
	height:50%;
	margin: 0;
	padding: 0;
}

#innerContact {
	color: #FFFFFF;
	margin: 0;
	position: relative;
	top: 50%;
	left: 50%;
	-ms-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}

