.mailto-wrapper {
	position: relative;
	}
.mailto-message {
	top: 0;
	left: 50%;
	transform: translate(-50%, -100%);
	margin-top: -15px;
	position: absolute;
	display: none;
	width: auto;
	white-space: nowrap;
	font-size: 12px;
	background-color: black;
	color: white;
	padding: 2px 6px;
	border-radius: 2px;
	&:after {
		content: '';
	}
}
.mailto-wrapper:hover .mailto-message {
	display: block;
}
